<script setup>
/* eslint-disable */
import { ref, watch, watchEffect, onMounted, reactive } from 'vue'
import { normal, lognormal, exponential, gamma, beta } from 'jstat';
import { booleanTypeAnnotation } from '@babel/types';

var binomial = require( '@stdlib/random-base-binomial' );

const data = [];
const sim_count = ref(0);
const ref_distribution = ref("$N(0,1)");

var svg = null;
var margin = { top: 20, right: 20, bottom: 50, left: 50 },
  width = 910 - margin.left - margin.right,
  height = 400 - margin.top - margin.bottom;

onMounted(() => {
  console.log("Hello World!");
  setup_histogram();
  plot_histogram();
  simulate()
});

function setup_histogram() {
  svg = d3.select("#d3")
    .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
}

function plot_histogram() {
  if(!svg) { return }
  svg.selectAll("*").remove()

  var dmin = d3.min(data),
      dmax = d3.max(data),
      dspread = dmax - dmin;

  var x = d3.scaleLinear()
    .domain([dmin - 0.05 * dspread, dmax + 0.05 * dspread])
    .range([0, width]);
  
  svg.append("g")
    .attr("transform", "translate(0," + height + ")")
    .call(d3.axisBottom(x));

  var histogram = d3.histogram()
    .domain(x.domain())
    .thresholds(x.ticks(100));

  // And apply this function to data to get the bins
  var bins = histogram(data);

  // Y axis: scale and draw:
  var y = d3.scaleLinear()
    .range([height, 0]);
  y.domain([0, d3.max(bins, function (d) { return d.length; })]);   // d3.hist has to be called before the Y axis obviously

  svg.append("g")
    .call(d3.axisLeft(y));

  // append the bar rectangles to the svg element
  svg.selectAll("rect")
    .data(bins)
    .enter()
    .append("rect")
    .attr("x", 1)
    .attr("transform", function (d) { return "translate(" + x(d.x0) + "," + y(d.length) + ")"; })
    .attr("width", function (d) { return x(d.x1) - x(d.x0) - 1; })
    .attr("height", function (d) { return height - y(d.length); })
    .style("fill", "#69b3a2");
}

var generator = () => normal.sample(0,1);

function simulate() {
  if (generator) {
    for(let i = 0; i < 1000; i++) data.push( generator() );
    sim_count.value = data.length;
  }
  setTimeout(simulate,10);
}

function update_stats() {
  plot_histogram();
}

watch(sim_count, update_stats);

watch(ref_distribution, () => {
  data.length = 0;
  let txt = ref_distribution.value;
  let ev = "() => (" + txt.replace(/\$/g,"this.") + ")";
  let scope = {
    N : normal.sample,
    E : exponential.sample,
    G : gamma.samle,
    Beta : beta.sample,
    B : binomial,
    Ber : (p) => binomial(1, p),
  }
  try {
    // https://stackoverflow.com/questions/8403108/calling-eval-in-particular-context
    let g = function() { return eval(ev) }.call(scope);
    if (typeof g() == "number") { generator = g; }
    else { throw typeof g() }
    console.log(ev);
  } 
  catch (err) { 
    console.log(ev, err);
    generator = null;
  }
})

</script>

<template>
  <h1>Stats Calculator </h1>
  <div style="float:right; text">Iterations {{ sim_count }}</div>
  <div id="d3"></div>



<table>
  <tbody>
  <tr style="background-color:#EEE"><td colspan="4" style="text-align:left;font-weight:bold">
    # Parameters
    </td></tr>
    <tr>
      <td>Distribution</td>
      <td><input type="text" v-model="ref_distribution" style="width:100%"></td>
    </tr>
</tbody>
</table>


  <p>
    Hello Katex!
    $$ f(x) = \int_0^x e^y dy $$
  </p>
</template>

